import { Formik } from 'formik';
import React, { Dispatch, SetStateAction, useEffect } from 'react';
import { Button, Form, Grid, Segment } from 'semantic-ui-react';
import { TEMPLATE_CLOSE, TEMPLATE_DELIMETER, TEMPLATE_OPEN } from './utils/constants';
import { TInputTypeObject, TOutputFormat } from './utils/types/setupTypes';
import * as Yup from 'yup';
import { FormValuesDisplay } from './FormValuesDisplay';
import { IFormFieldList, IVocabulary } from '../interfaces';
import { RenderLayoutInput } from './renderers/RenderLayoutInput';
import { useDyamicFormInitializer } from '../hooks/useDynamicFormInitializer';

type TProps = {
	data: string;
	dataMapping: IFormFieldList[];
	inputTypes: TInputTypeObject[];
	output: TOutputFormat[];
	vocabulary: IVocabulary[];
	setOutput: Dispatch<SetStateAction<TOutputFormat[]>>;
};

const floorDummyData = [
	{ floor: 1, length: 12, height: 41, width: 52, area: 523 },
	{ floor: 2, length: 12, height: 41, width: 52, area: 523 },
];

const generateOutput = (rawHtmlString: string, dataMapping: { [key: string]: string }) => {
	let output = [];
	if (rawHtmlString.includes(TEMPLATE_OPEN)) {
		const [currentContent, ...rest] = rawHtmlString.split(TEMPLATE_OPEN);
		output.push({
			input: false,
			inline: true,
			content: currentContent.split(/<p>/).join('<div>').split(/<\/p>/).join('</div>'),
			id: output.length,
		});
		rest.forEach((openSplit) => {
			if (openSplit.includes(TEMPLATE_CLOSE)) {
				const [contentIdentifier, ...closeSplit] = openSplit.split(TEMPLATE_CLOSE);
				if (contentIdentifier.startsWith('data')) {
					output.push({
						input: false,
						content: dataMapping[contentIdentifier.split(TEMPLATE_DELIMETER)[1]],
						inline: true,
						id: output.length,
					});
				} else {
					output.push({
						id: output.length,
						input: true,
						content: contentIdentifier,
					});
				}
				output.push({
					id: output.length,
					input: false,
					// inline: true,
					content: closeSplit.join().split(/<p>/).join('<div>').split(/<\/p>/).join('</div>'),
				});
			} else {
				output.push({
					id: output.length,
					input: false,
					content: openSplit.replace(/<p>/, '<div>'),
				});
			}
		});
	} else {
		output = [
			{
				id: output.length,
				input: false,
				content: rawHtmlString.replace(/<p>/, '<div>'),
			},
		];
	}
	return output;
};

export const Preview = ({ data, dataMapping, inputTypes, output, setOutput, vocabulary }: TProps) => {
	const { initialValues, getDataToSend } = useDyamicFormInitializer(inputTypes, {}, { floor: floorDummyData });

	useEffect(
		() => {
			//@ts-ignore
			const formattedData = generateOutput(data, dataMapping);
			setOutput(formattedData);
		},
		// eslint-disable-next-line react-hooks/exhaustive-deps
		[data]
	);

	const generateSchema = () => {
		let schema = {};
		inputTypes!.forEach((inputType) => {
			const validation = inputType && inputType.meta && inputType.meta.validation;
			if (validation) {
				if (validation.required) {
					if (validation.dataType === 'string') {
						Object.assign(schema, {
							[inputType.name]: Yup.string().required('Required'),
						});
					} else if (validation.dataType === 'number') {
						Object.assign(schema, {
							[inputType.name]: Yup.number().typeError('Must be a number').required('Required'),
						});
					}
				} else {
					if (validation.dataType === 'number') {
						Object.assign(schema, {
							[inputType.name]: Yup.number().typeError('Must be a number').required('Required'),
						});
					}
				}
			}
		});
		return Yup.object().shape(schema);
	};

	return (
		<Form>
			<Grid>
				<Grid.Row>
					<Grid.Column>
						<h3>Preview</h3>
					</Grid.Column>
					{/* <Grid.Column width="5">
						<Button
							onClick={() =>
								//@ts-ignore
								setOutput(generateOutput(data, dataMapping))
							}
							content="Generate"
						/>
					</Grid.Column> */}
				</Grid.Row>
				<Grid.Row>
					<Formik
						enableReinitialize
						initialValues={initialValues}
						validationSchema={generateSchema()}
						onSubmit={(values, { setSubmitting }) => {
							// setTimeout(() => {
							console.log(JSON.stringify(values, null, 2));
							//   setSubmitting(false);
							// }, 400);
						}}
					>
						{(props) => (
							<>
								<Grid.Column width="12">
									<Segment style={{ textAlign: 'left' }}>
										{output &&
											output.map((row) => (
												<React.Fragment key={row.id}>
													{row.input ? (
														<RenderLayoutInput
															content={row.content}
															formikProps={props}
															inputTypes={inputTypes}
															vocabulary={vocabulary}
															permitData={{ floor: floorDummyData }}
														/>
													) : (
														<div
															style={{
																display: row.inline ? 'inline-block' : 'inline',
															}}
															dangerouslySetInnerHTML={{ __html: row.content }}
														/>
													)}
												</React.Fragment>
											))}
										{/* {formattedData && (
                )} */}
										{/* <div dangerouslySetInnerHTML={{ __html: formattedData }} /> */}
										<Button
											content="Submit"
											onClick={(e) => {
												e.preventDefault();
												props.handleSubmit();
											}}
										/>
									</Segment>
								</Grid.Column>
								<Grid.Column width="4">
									<FormValuesDisplay values={getDataToSend(props.values, '7778000001')} />
								</Grid.Column>
							</>
						)}
					</Formik>
				</Grid.Row>
			</Grid>
		</Form>
	);
};
